import React, { useState } from 'react';

import BuildView from '../../../components/BuildView'

import {
  Button,
  Grid,
  Typography
} from '@material-ui/core'

import { colors } from '../../../styles/theme'

import { saveBuild } from '../../../state/actions/ethereal.actions'

const FragmentBuildReview = (props) => {
  const {
    data
  } = props

  const [buttonDisabled, setButtonDisabled] = useState(false)

  console.log(data)

  const save = async () => {
    setButtonDisabled(false) // true
    const response = await saveBuild(data)

    console.log(response)
  }

  return (
    <Grid container 
      alignContent='center'
      alignItems='center'>
        { /*
        <Grid item xs={12}>
          <Button disabled={buttonDisabled}
            onClick={save}
            style={styles.saveButton}>Save Build</Button>
        </Grid>
        */ }
        <Grid item xs={12}>
          <Typography style={styles.previewBuildHeader}>
            Preview Build
          </Typography>
        </Grid>
        <Grid item xs={12}>
          <BuildView data={data} />
        </Grid>
        { /*
        <Grid item xs={12}>
          <Button disabled={buttonDisabled}
            onClick={save}
            style={styles.saveButton}>Save Build</Button>
        </Grid>
        */ }
    </Grid>
  )
}

export default FragmentBuildReview;

const styles = {
  header: {
    color: colors.gray
  },
  saveButton: {
    backgroundColor: colors.theme.goldBackground
  }
}