import React, { useEffect, useState } from 'react';

import {
  Button,
  Grid,
  TextField,
  Typography
} from '@material-ui/core'

import { makeStyles } from '@material-ui/core/styles'
import { colors } from '../../../styles/theme'

import {
  getMythList
} from '../../../state/actions/ethereal.actions'

const FragmentBuildMyths = (props) => {
  const {
    data,
    setData
  } = props

  const [count, setCount] = useState(0)
  const [myths, setMyths] = useState([])
  const [roles, setRoles] = useState([])

  useEffect(() => {
    const retrieveMyths = async () => {
      const mythList = await getMythList()
      setMyths(mythList.sort((a, b) => (a.name > b.name) ? 1 : -1))
    }

    const retrieveRoles = async () => {
      setRoles([{
        name: 'Ice',
        image: '/images/roles/ice.png'
      },{
        name: 'Jungle',
        image: '/images/roles/jungle.png'
      },{
        name: 'Void',
        image: '/images/roles/void.png'
      },{
        name: 'Fire',
        image: '/images/roles/fire.png'
      },{
        name: 'Support',
        image: '/images/roles/support.png'
      }])
    }

    retrieveMyths()
    retrieveRoles()
  }, [])  

  const useStyles = makeStyles({
    root: {
      "& .MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline": {
        border: `1px solid ${colors.theme.grey}`,
        color: colors.theme.white
      },
      "&:hover .MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline": {
        border: `1px solid ${colors.theme.grey}`,
        color: colors.theme.white
      },
      "& .MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline": {
        border: `1px solid ${colors.theme.grey}`,
        color: colors.theme.white
      }
    },
    
    input: {
      color: colors.theme.white
    },
  })

  const classes = useStyles()

  const saveTitle = (title) => {
    let tempData = data
    tempData.title = title
    
    setData(tempData)
  }

  const saveMyth = (myth) => {
    let tempData = data
    tempData.data = myth
    tempData.data.image = myth.backgroundURLs[0]

    tempData.data.abilities.map((_, index) => {
      tempData.data.abilities[index].levels = []
    })
    tempData.data.abilities = [{name: tempData.data.class.className, imageURL: tempData.data.class.imageURL, levels: []}].concat(tempData.data.abilities)

    setData(tempData)
    setCount(count + 1)
  }

  const saveRole = (role) => {
    let tempData = data
    tempData.role = role
    
    setData(tempData)
    setCount(count + 1)
  }

  const saveBackground = (background) => {
    let tempData = data
    tempData.data.background = background
    
    setData(tempData)
    setCount(count + 1)
  }

  return (
    <div>
      <Grid container style={styles.content}>
          <Grid item xs={12}>
            <Typography 
              style={styles.label}
              required
            >1. Name your Build</Typography>
            <TextField 
              name="title"
              className={ classes.root }
              InputProps={{ className: classes.input }}
              fullWidth
              variant="outlined"
              defaultValue={data.title}
              onChange={(e) => { 
                  saveTitle(e.target.value)
              }}
            />
          </Grid>
          
          <Grid item xs={12}>
            <Typography style={styles.label} xs={12}>2. Select Role</Typography>
            <Grid container>
            { /* TODO: Convert to loop from external source / api */ }
              { roles.map((role, index) =>
                <Grid item xs key={`role-${role.name}`} >
                  <Grid container style={styles.roleContainer} >
                    <Grid item xs={12}>
                      <Button onClick={() => saveRole(role)}>
                        <img style={{ 
                            ...styles.roleImage,
                            opacity: data.role?.name === role.name ? 1 : 0.2 
                          }}
                          width={60}
                          height={60}
                          src={`${process.env.PUBLIC_URL}/${role.image}`} 
                          alt={`${role.name} role`} />
                      </Button>
                    </Grid>
                    <Grid item xs={12}>
                      <Typography style={styles.roleDescription}>
                        {role.name}
                      </Typography>
                    </Grid>
                  </Grid>
                </Grid>
              )}
                <Grid item xs={6} />
            </Grid>
          </Grid>  

          <Grid item xs={12}>
            <Typography  style={styles.label} xs={12}>3. Select Myth</Typography>
            <Grid container>
              { myths.map((myth, index) =>
                <Grid item xs
                  key={`myth-${index}`}
                  style={{ textAlign: 'center', paddingBottom: 15 }}>
                  <Button onClick={() => saveMyth(myth)}
                    style={{opacity: data?.data?.name === myth.name ? 1 : 0.5 }}>
                      <svg xmlns="http://www.w3.org/2000/svg" width="100" height="100" viewBox="0 0 62 62">
                        <defs>
                          <clipPath id="mythClipPath">
                            <path d="M212.771,337.46l-8.666,10.5v49.5h51.334l8.666-10.5v-49.5Z" 
                            transform="translate(-203.605 -336.96)" />
                          </clipPath>
                        </defs>
                        <image href={myth.backgroundURLs[0]} width={120} height={61} x={-27} y={0} clipPath="url(#mythClipPath)" />
                        <path d="M212.771,337.46l-8.666,10.5v49.5h51.334l8.666-10.5v-49.5Z" 
                            transform="translate(-203.605 -336.96)" stroke={colors.theme.goldPrimary} fill="none" strokeWidth="1" />
                      </svg>
                  </Button>
                  <Typography>
                    {myth.name}
                  </Typography>
                </Grid>
              )}
            </Grid>
          </Grid>

          <Grid item xs={12}>  
            <Typography style={styles.label}>4. Select Background</Typography>
            <Grid container>
            { data.data && data.data.backgroundURLs.map((background, index) =>
              <Grid item style={styles.backgroundImageWrapper}
                key={`background-${background}`}>
                <Button onClick={() => saveBackground(background) }
                  style={{opacity: data.data.background === background ? 1 : 0.5}}>
                    <svg xmlns="http://www.w3.org/2000/svg" width="275" height="125" viewBox="0 0 221.999 104">
                      <clipPath id="backgroundClipPath">
                      <path d="M517.123,345.19l-14.742,17.854V447.19H707.638l14.742-17.854V345.19Z" transform="translate(-501.381 -344.19)"
                        fill="none" stroke={colors.theme.goldPrimary} strokeMiterlimit="10" strokeWidth="2"/>
                      </clipPath>
                      <image href={background} width={275} height={125} x={-27} y={0} clipPath="url(#backgroundClipPath)" />
                      <path d="M517.123,345.19l-14.742,17.854V447.19H707.638l14.742-17.854V345.19Z" transform="translate(-501.381 -344.19)"
                        fill="none" stroke={colors.theme.goldPrimary} strokeMiterlimit="10" strokeWidth="2"/>
                    </svg>
                </Button>
              </Grid>
            )}
            </Grid>
          </Grid>
      </Grid>
    </div>
  )
}

export default FragmentBuildMyths;

const styles = {
  content: {
    alignContent: 'center',
    alignItem: 'center'
  },

  label: {
    marginTop: 30,
    marginBottom: 10,
    color: colors.theme.white
  },

  roleContainer: {
    textAlign: 'center'
  },
  roleImage: {
    
  },
  roleDescription: {
    color: colors.theme.white
  },
  roleUnselected: {
    opacity: 90
  },

  backgroundImageWrapper: {
    marginLeft: 15,
    marginRight: 15,
    marginBottom: 15,
  }
}