import React, { useEffect, useState } from 'react';

import {
  Grid,
  MenuItem,
  Select,
  Typography
} from '@material-ui/core'

import { colors } from '../../../styles/theme'

import { makeStyles } from '@material-ui/core/styles'

import {
  SelectedStat,
  StatPicker
} from '../../../components/Builds'

const FragmentBuildLoadout = (props) => {
  const {
    data,
    setData
  } = props
  
  const [combatTactics, setCombatTactics] = useState([])
  const [count, setCount] = useState(0)

  const statCosts = [1, 2, 3, 4]
  const statColors = [colors.theme.statsFirst, colors.theme.statsSecond, colors.theme.statsThird, colors.theme.statsFourth]
  
  useEffect(() => {
    const retrieveCombatTactics = async (id) => {
      //setCombatTactics(await getCombatTacticsList())
      setCombatTactics([{
          name: 'Assault',
          stats: ['Attack Speed', 'Life Steal', 'Penetration']
        },{
          name: 'Demolition',
          stats: ['Crtical Chance', 'Critical Damage', 'Mythical Damage']
        },{
          name: 'Protect',
          stats: ['Armor', 'Defensive Power', 'Magic Resist']
        },{
          name: 'Reinforcement',
          stats: ['Health per 5', 'Mana per 5', 'Persistence']
        }
      ])
    }

    retrieveCombatTactics()
  }, [])

  const handleSelectedTactic = (event) => {
    let tempData = data
    tempData.remainingPoints = 8
    tempData.selectedStats = []
    tempData.primaryTactic = event.target.value
    tempData.secondaryTactic = 0

    let statList = []
    combatTactics.map((tactic, index) => {
      if(index !== event.target.value) {
        tactic.stats.map((stat) => {
          statList.push({
            tactic: tactic.name,
            stat: stat
          })
        })
      }
    })

    tempData.secondaryStatList = statList

    setData(tempData)
    setCount(count+1)
  }

  const handleSelectedSecondary = (event) => {
    let tempData = data
    tempData.selectedStats = tempData.selectedStats.filter(stat => stat.isPrimary)
    tempData.secondaryTactic = event.target.value
    
    tempData.remainingPoints = 8
    tempData.selectedStats.map((stat) => {
      tempData.remainingPoints = tempData.remainingPoints - stat.cost
    })
    
    setData(tempData)
    setCount(count+1)
  }

  const addStat = (stat) => {
    let tempData = data
    tempData.remainingPoints = tempData.remainingPoints - stat.cost

    tempData.selectedStats.push({
      ...stat
    })
    
    setData(tempData)
    setCount(count+1)
  }

  const removeStatAt = (index) => {
    let tempData = data

    const stat = tempData.selectedStats[index]
    tempData.selectedStats.splice(index, 1)
    tempData.remainingPoints = tempData.remainingPoints + stat.cost
    
    setData(tempData)
    setCount(count+1)
  }

  const useStyles = makeStyles({
    selectDropdown: {
      border: `1px solid ${colors.theme.white}`,
      color: colors.theme.white,
      width: '100%',

      ".MuiSvgIcon-root .MuiSelect-icon": {
        color: colors.theme.white
      }
    },

    menuItem: {
     marginLeft: 10 
    }
  })

  const classes = useStyles()

  return (
    <Grid container 
      alignContent='center'
      alignItems='center'>
        <Grid item xs={12} style={styles.tacticsContainer}>
          <Grid container style={{alignItems: 'center', alignContent: 'center' }}>
            <Grid item xs={7} style={{alignItems: 'center', alignContent: 'center'}}>
              <Typography style={styles.label}>Selected Combat Stats</Typography>
            </Grid>
            <Typography style={styles.label}>Stat Costs:</Typography>
            { statCosts.map((cost, index) => 
                  <Typography style={{
                      ...styles.statCosts,
                      backgroundColor: statColors[index], 
                      color: index < 3 ? colors.theme.black : colors.theme.white
                    }}>{cost}</Typography>
            )}
            <Grid item xs={2}>
              <Typography style={{...styles.label, textAlign: "right"}}>Remaining Points:</Typography>
            </Grid>
            <Grid item xs>
              <Typography style={styles.remainingPoints}>{data.remainingPoints}</Typography>
            </Grid>
          </Grid>
          
          <Grid container style={styles.statContainer}>
              { data.selectedStats.map((stat, index) => 
              <Grid item xs={3} style={styles.selectedStat}>
                <SelectedStat stat={stat} removeStat={() => removeStatAt(index)} />
              </Grid>
              )}
          </Grid>

          <Grid container>
            <Grid item xs>
              <Grid container>
                <Grid item xs={12}>
                  <Typography style={styles.label}>Primary Combat Tactic</Typography>
                </Grid>
                <Grid item xs={12}>
                  <Select
                    value={data.primaryTactic}
                    onChange={handleSelectedTactic}
                    className={ classes.selectDropdown } 
                    inputProps={{ className:classes.menuItem }}>
                      { !(data.primaryTactic >= 0) &&
                      <MenuItem 
                        className={classes.menuItem}
                        value={-1}>Pick Primary Tactic</MenuItem>
                      }
                    { combatTactics && combatTactics.map((tactic, index) =>
                      <MenuItem className={classes.menuItem}
                        value={index}>{tactic.name}</MenuItem>
                    )}
                  </Select>
                </Grid>
                <Grid item xs={12}>
                  <Grid container>
                    { combatTactics[data.primaryTactic] && combatTactics[data.primaryTactic].stats.map((stat, index) => 
                      <Grid item xs={12}>
                        <StatPicker isPrimary={true} 
                          remainingPoints={data.remainingPoints}
                          name={stat} 
                          addStat={addStat} />
                      </Grid>
                    )}
                  </Grid>
                </Grid>
              </Grid>
            </Grid>
            <Grid item xs={1}></Grid>
            <Grid item xs>
              <Grid container>
                <Grid item xs={12}>
                  <Typography style={styles.label}>Secondary Combat Tactic</Typography>
                </Grid>
                <Grid item xs={12}>
                  <Select
                    disabled={data.primaryTactic === -1}
                    width={1}
                    value={data.secondaryTactic}
                    onChange={handleSelectedSecondary}
                    className={ classes.selectDropdown }
                    inputProps={{ className:classes.menuItem }}
                    >
                      { data.primaryTactic === -1 &&
                      <MenuItem />
                      }
                      { data.secondaryStatList && data.secondaryStatList.map((stat, index) =>
                      <MenuItem value={index}>{`[${stat.tactic}] ${stat.stat}`}</MenuItem>
                      )}
                  </Select>
                </Grid>
                <Grid item xs={12}>
                  <Grid container>
                    <Grid item xs={12}>
                      { data.secondaryTactic >= 0 &&
                      <StatPicker isPrimary={false} 
                        name={data.secondaryStatList[data.secondaryTactic].stat} 
                        remainingPoints={data.remainingPoints}
                        addStat={addStat} />
                      }
                    </Grid>
                  </Grid>
                </Grid>
              </Grid>
            </Grid>
            <Grid item xs={12} style={styles.disclaimer}>
              <Typography>The stats below are Etherean.GG's best guess at what to expect in the loadout system. <br />These are subject to change when ECOS releases.</Typography>
            </Grid>
          </Grid>
        </Grid>
        { /* 
        <Grid item xs={12} style={styles.familiarContainer}>
          <Grid container>
            <Grid item xs={12} style={{alignItems: 'center', alignContent: 'center'}}>
              <Typography style={styles.label}>2. Select Familiar</Typography>
            </Grid>
            <Grid item xs={12}>
              <Grid container>
                <Grid item xs={1} style={{ 
                    background: "no-repeat center/150% url('https://undyinggames.com/assets/Images/News/FamiliarCard.png')", 
                    border: `1px solid ${colors.theme.goldPrimary}`,
                    marginTop: 10,
                    height: 150, 
                    width: 250 }}>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
        */ }
    </Grid>
  )
}

export default FragmentBuildLoadout;

const styles = {
  disclaimer: {
    textAlign: 'center',
    marginTop: 20
  },
  header: {
    color: colors.gray
  },
  label: {
    font: 10,
    color: colors.theme.white,
    marginRight: 2
  },

  tacticOption: {
    paddingLeft: 10,
    color: colors.theme.white
  },

  familiarContainer: {
    marginTop: 25
  },

  remainingPoints: {
    background: colors.theme.goldPrimary,
    color: colors.theme.black,
    
    width: 25,
    marginLeft: 10,
    textAlign: "center",
    alignContent: "center",
    alignItems: "center",
    
    border: 1,
    borderRadius: 3,
    borderColor: colors.white
  }, 
  statCosts: {
    border: `1px solid ${colors.theme.goldBackground}`,
    borderRadius: 5,
    width: 20, 
    margin: 5, 
    marginRight: 2, 
    
    textAlign: "center",
    alignContent: "center",
    alignItems: "center"
  },
  statContainer: {
    minHeight: 175,
    marginTop: 15,
    marginBottom: 25
  },
  selectedStat: {
    height: 75,
    marginTop: 5,
    marginBottom: 5,
    paddingLeft: 10,
    paddingRight: 10
  }
}