import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom'

import {
  Grid
} from '@material-ui/core'

import BuildView from '../../components/BuildView'
import { searchBuildsBySlug } from '../../state/actions/ethereal.actions'

const ViewBuild = () => {
  document.title = "Etherean.gg - View Build"

  const { buildSlug } = useParams()
  const [build, setBuild] = useState({})

  useEffect(() => {      
    // browser throws an error if you don't define it and then call it ¯\_(ツ)_/¯
    const retrieveBuild = async (buildSlug) => {
      setBuild(await searchBuildsBySlug(buildSlug))
    }

    retrieveBuild(buildSlug)
  }, [])

  return (
    build?.id ? 
    <Grid container>
      <BuildView data={build} />
    </Grid> :
    <Grid>
      No Build Found
    </Grid>
  );
}

export default ViewBuild;